<template>
  <div class="storage-x">
    <div class="el-icon-close" @click="$emit('closehandle', 1)"></div>
    <div class="storage-name">企业安全情况</div>
    <div class="paddingC">
      <ul class="principal safety-count flex">
        <li>企业法人：</li>
        <li>方胜</li>
        <li>企业安管人：</li>
        <li>方胜</li>
      </ul>
      <ul class="safety-count flex">
        <li>安全生产天数：</li>
        <li>100</li>
      </ul>
      <ul class="total-count flex">
        <li>安全检查问题总数：</li>
        <li>8</li>
        <li>9</li>
        <li>0</li>
      </ul>
      <div class="problem-items">
        <ul class="problem-items-title flex">
          <li class="color-blue">已整改问题</li>
          <li class="color-darkblue">待复查问题</li>
          <li class="color-orange">整改中问题</li>
        </ul>
        <ul class="problem-items-count flex">
          <li><span class="color-blue">800</span> 项</li>
          <li><span class="color-darkblue">80</span> 项</li>
          <li><span class="color-orange">10</span> 项</li>
        </ul>
      </div>
      <div id="nightingale"></div>
      <div class="hidden-total">
        <h4>隐患总数：<span class="color-darkblue">1000</span> 项</h4>
        <ul class="flex">
          <li id="liquidFill1"></li>
          <li id="liquidFill2"></li>
          <li id="liquidFill3"></li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import * as echarts from 'echarts'
import 'echarts-liquidfill/src/liquidFill.js'
export default {
  name: '',
  data() {
    return {}
  },
  methods: {
    // 南丁格尔玫瑰图
    showChart() {
      let chart = echarts.getInstanceByDom(
        document.getElementById('nightingale')
      )
      if (chart == null) {
        // 如果不存在，就进行初始化
        chart = echarts.init(document.getElementById('nightingale'))
      }

      let option = {
        legend: {
          show: false,
        },
        series: [
          {
            name: '面积模式',
            type: 'pie',
            radius: [10, 65],
            center: ['50%', '50%'],
            roseType: 'area',
            itemStyle: {},
            label: {
              color: '#fff',
            },
            labelLine: {
              length: 10,
              length2: 2,
            },
            data: [
              { value: 40, name: '基础管理' },
              { value: 38, name: '作业现场' },
              { value: 32, name: '设备设施' },
              { value: 30, name: '消防管理' },
              { value: 28, name: '个体防护' },
              { value: 26, name: '相关方作业' },
              { value: 22, name: '疫情防控' },
              { value: 18, name: '其他' },
            ],
          },
        ],
      }

      chart.clear()
      chart.setOption(option)

      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })

      // 事件
      chart.on('click', (params) => {
        // console.log(params)
        this.$emit('showgridhandle', params.data)
      })
    },

    // 水晶球
    echartsMit() {
      let chart = echarts.getInstanceByDom(
        document.getElementById('liquidFill1')
      )
      if (chart == null) {
        // 如果不存在，就进行初始化
        chart = echarts.init(document.getElementById('liquidFill1'))
      }

      let option = {
        title: {
          text: '已整改\n840项',
          left: 'center',
          top: 20,
          padding: [50, 0, 0, 0],
          textStyle: {
            color: '#fff',
            fontSize: 12,
            lineHeight: 16,
          },
        },
        series: [
          {
            type: 'liquidFill',
            data: [0.64],
            radius: '60%', //水晶球大小
            center: [50, 30], //水晶球位置
            color: ['#1DC8FC'],
            itemStyle: {
              shadowBlur: 0,
            },
            outline: {
              borderDistance: 0,
              itemStyle: {
                borderWidth: 1,
                borderColor: '#156ACF',
                shadowBlur: 20,
              },
            },
            backgroundStyle: {
              color: 'transparent',
            },
            label: {
              color: 'white',
              insideColor: 'white',
              fontSize: 12,
            },
          },
        ],
      }

      chart.clear()
      chart.setOption(option)

      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },

    // 球2
    echartsMit2() {
      let chart = echarts.getInstanceByDom(
        document.getElementById('liquidFill2')
      )
      if (chart == null) {
        // 如果不存在，就进行初始化
        chart = echarts.init(document.getElementById('liquidFill2'))
      }

      let option = {
        title: {
          text: '整改中\n100项',
          left: 'center',
          top: 20,
          padding: [50, 0, 0, 0],
          textStyle: {
            color: '#fff',
            fontSize: 12,
            lineHeight: 16,
          },
        },
        series: [
          {
            type: 'liquidFill',
            data: [0.43],
            radius: '60%', //水晶球大小
            center: [50, 30],
            color: ['#3573FC'],
            itemStyle: {
              shadowBlur: 0,
            },
            outline: {
              borderDistance: 0,
              itemStyle: {
                borderWidth: 1,
                borderColor: '#156ACF',
                shadowBlur: 20,
              },
            },
            backgroundStyle: {
              //整体背景设置
              color: 'transparent',
            },
            label: {
              color: 'white',
              insideColor: 'white',
              fontSize: 12,
            },
          },
        ],
      }

      chart.clear()
      chart.setOption(option)

      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },

    // 球3
    echartsMit3() {
      let chart = echarts.getInstanceByDom(
        document.getElementById('liquidFill3')
      )
      if (chart == null) {
        // 如果不存在，就进行初始化
        chart = echarts.init(document.getElementById('liquidFill3'))
      }

      let option = {
        title: {
          text: '超期未整改\n60项',
          left: 'center',
          top: 20,
          padding: [50, 0, 0, 0],
          textStyle: {
            color: '#fff',
            fontSize: 12,
            lineHeight: 16,
          },
        },
        series: [
          {
            type: 'liquidFill',
            data: [0.16],
            radius: '60%', //水晶球大小
            center: [50, 30],
            color: ['#FA6935'],
            backgroundStyle: {
              color: 'transparent',
            },
            itemStyle: {
              shadowBlur: 0,
            },
            outline: {
              borderDistance: 0,
              itemStyle: {
                borderWidth: 1,
                borderColor: '#156ACF',
                shadowBlur: 20,
              },
            },
            label: {
              color: 'white',
              insideColor: 'white',
              fontSize: 12,
            },
          },
        ],
      }

      chart.clear()
      chart.setOption(option)

      // 响应式
      window.addEventListener('resize', () => {
        chart.resize()
      })
    },
  },
  mounted() {
    this.$nextTick(function () {
      this.showChart()
      this.echartsMit()
      this.echartsMit2()
      this.echartsMit3()
    })
  },
}
</script>
<style lang="less" scoped>
// 整体配置
.storage-x {
  box-sizing: border-box;
  width: 340px;
  height: 540px;
  // background: url(../../assets/images/ju.png) no-repeat;
  background-color: #032d5673;
  border: 1px solid #019eff;
  border-radius: 5px;
  position: absolute;
  right: 300px;
  top: 50px;
  z-index: 1000;
  color: white;

  .storage-name {
    width: 139px;
    height: 42px;
    font-weight: 300;
    line-height: 42px;
    text-align: center;
    border: 1px solid #00f;
    background: url(../../assets/images/ju2.png) no-repeat;
    position: absolute;
  }
}

.paddingC {
  // padding: 20px 20px 20px 40px;
  padding: 10px 20px;
  font-size: 12px;
}
.principal {
  margin-top: 40px;
  margin-bottom: 6px;

  li:nth-of-type(2) {
    margin-right: 40px;
  }
}
.safety-count {
  justify-content: flex-start;
  font-size: 14px;

  li:nth-of-type(even) {
    font-size: 16px;
    font-weight: bold;
  }
}
.total-count {
  margin-top: 6px;
  justify-content: flex-start;
  font-size: 14px;

  li:first-of-type ~ li {
    width: 18px;
    height: 18px;
    margin-right: 4px;
    color: #14e6f7;
    font-size: 16px;
    font-weight: bold;
    line-height: 18px;
    text-align: center;
    border: solid 1px #14e6f7;
  }
}

.problem-items {
  margin: 10px 0;

  &-count {
    box-sizing: border-box;
    margin: 8px 0;
    padding: 2px 0;
    background-color: rgba(34, 146, 166, 0.2);
    box-shadow: 0 1px 2px 1px #2292a6;
    border-radius: 10px;
  }

  span {
    font-size: 20px;
  }

  .color-blue {
    color: #14e5f6;
  }
}

// 南丁格尔玫瑰图
#nightingale {
  height: 190px;
  width: 100%;
}

// 隐患总数水波球
.hidden-total {
  height: 120px;
  margin-top: 10px;
  h4 {
    margin-left: 4px;
    font-weight: 400;
  }

  li {
    width: 100px;
    height: 100px;
    margin-top: 10px;
  }
}

// 关闭按钮
.el-icon-close {
  float: right;
  margin-right: 20px;
  margin-top: 10px;
  font-size: 20px;
  cursor: pointer;
}
</style>